import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';

const ReactNativeMap = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/fullstack"}>{t('full_stack.title')}</Link> / <Link to={langUrl + "/fullstack/reactnativemap"}>React Native App with Map</Link>
          </p>
          <h4 className="text-center">React Native App with Map</h4>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12">
          <div className="list row">
            <div className="col-md-12 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeMap/icon.png'} alt='' />
            </div>
            <div className="col-md-12 text-center">
              <p className="page-text mt-3">{t('reactnativemap.download')}:<br/><a href="https://github.com/ksin8909/ReactNativeMap/tree/main/apk" target="_blank" rel="noreferrer">https://github.com/ksin8909/ReactNativeMap/tree/main/apk</a></p>
            </div>
          </div>
          <div className="list row">
            <div className="col-md-12 text-center">
              <p className="page-text mt-3">{t('reactnativemap.screenshot')}:</p>
            </div>
          </div>
          <div className="list row mb-3">
            <div className="col-md-3 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeMap/1.webp'} alt='' />
            </div>
            <div className="col-md-3 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeMap/2.webp'} alt='' />
            </div>
            <div className="col-md-6 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeMap/3.webp'} alt='' />
            </div>
          </div>
        </div>
      </div>
    </div>
    </>
  );
}

export default ReactNativeMap
