import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const TsParticlesConfetti = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const particlesInit = async (main:any) => {

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };

  return (
    <>
      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/effect/tsparticles"}>tsParticles</Link> / <Link to={langUrl + "/effect/tsparticles/confetti"}>Confetti</Link>
            </p>
            <h4 className="text-center">Confetti</h4>
          </div>
        </div>
      </div>
      <div className="row m-0">
        <div className="tsparticles-wrapper col-md-12">
        <Particles
            id="tsparticles-confetti"
            init={particlesInit}
            options={{
              fullScreen: { enable: false },
              background: {
                color: {
                  value: "#000000",
                },
              },
              fpsLimit: 60,
              detectRetina: true,
              particles: {
                number: {
                  value: 0
                },
                color: {
                  value: "#f00"
                },
                shape: {
                  type: ["circle", "square", "polygon"],
                  options: {
                    polygon: {
                      sides: 6
                    }
                  }
                },
                opacity: {
                  value: { min: 0, max: 1 },
                  animation: {
                    enable: true,
                    speed: 1,
                    startValue: "max",
                    destroy: "min"
                  }
                },
                size: {
                  value: { min: 3, max: 7 }
                },
                life: {
                  duration: {
                    sync: true,
                    value: 7
                  },
                  count: 1
                },
                move: {
                  enable: true,
                  gravity: {
                    enable: true
                  },
                  drift: {
                    min: -2,
                    max: 2
                  },
                  speed: { min: 10, max: 30 },
                  decay: 0.1,
                  direction: "none",
                  random: false,
                  straight: false,
                  outModes: {
                    default: "destroy",
                    top: "none"
                  }
                },
                rotate: {
                  value: {
                    min: 0,
                    max: 360
                  },
                  direction: "random",
                  animation: {
                    enable: true,
                    speed: 60
                  }
                },
                tilt: {
                  direction: "random",
                  enable: true,
                  value: {
                    min: 0,
                    max: 360
                  },
                  animation: {
                    enable: true,
                    speed: 60
                  }
                },
                roll: {
                  darken: {
                    enable: true,
                    value: 25
                  },
                  enable: true,
                  speed: {
                    min: 15,
                    max: 25
                  }
                },
                wobble: {
                  distance: 30,
                  enable: true,
                  speed: {
                    min: -15,
                    max: 15
                  }
                }
              },
              emitters: {
                direction: "none",
                spawnColor: {
                  value: "#ff0000",
                  animation: {
                    h: {
                      enable: true,
                      offset: {
                        min: -1.4,
                        max: 1.4
                      },
                      speed: 0.1,
                      sync: false
                    },
                    l: {
                      enable: true,
                      offset: {
                        min: 20,
                        max: 80
                      },
                      speed: 0,
                      sync: false
                    }
                  }
                },
                life: {
                  count: 0,
                  duration: 0.1,
                  delay: 0.6
                },
                rate: {
                  delay: 0.1,
                  quantity: 100
                },
                size: {
                  width: 0,
                  height: 0
                }
              }
            }}
          />
        </div>

      </div>
    </>
  )
}

export default TsParticlesConfetti