import { useTranslation } from 'react-i18next';
import { useWeb3React } from '@web3-react/core';
import { WalletLinkConnector } from "@web3-react/walletlink-connector";
import { WalletConnectAppEthBalance } from "./walletconnect-app-balance.component";
import {SWRConfig} from "swr";

const CoinbaseWalletApp = () => {

  //#region
  const CoinbaseWallet = new WalletLinkConnector({
    url: 'https://mainnet.infura.io/v3/'+process.env.REACT_APP_INFURA_IO_KEY,
    appName: "Test React",
    supportedChainIds: [1, 3, 4, 5, 42],
  });

  const { activate, deactivate, active, chainId, account, library } = useWeb3React();

  async function connect() {
    await activate(CoinbaseWallet);
  }

  async function disconnect() {
    await deactivate();
  }

  const fetcher = (library: any) => (...args:any[]) => {
    const [arg1, arg2, ...params] = args
    const method = arg1
    return library[method](arg2, ...params)
  }
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
      {active
        ? <button onClick={disconnect} className="btn btn-success">
            {t('coinbasewallet.button_disconnect')}
          </button>
        : <button onClick={connect} className="btn btn-success">
            {t('coinbasewallet.button_connect')}
          </button>
      }
      <h5 className="mt-3">{t('coinbasewallet.my_account')}:</h5>
      <p className="text-break">{t('coinbasewallet.address')}: {account}</p>
      <p className="text-break">{t('coinbasewallet.eth_balance')}: {active && (
            <SWRConfig value={{ fetcher: fetcher(library) }}>
              <WalletConnectAppEthBalance />
            </SWRConfig>
        )}
      </p>
      <p className="text-break">{t('coinbasewallet.chain_id')}: {chainId}</p>
    </>
  );
}

export default CoinbaseWalletApp