import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';

const ReactNativeCamera = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/fullstack"}>{t('full_stack.title')}</Link> / <Link to={langUrl + "/fullstack/reactnativecamera"}>React Native App with Camera</Link>
          </p>
          <h4 className="text-center">React Native App with Camera</h4>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12">
          <div className="list row">
            <div className="col-md-3 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactnNativeMap/1.webp'} alt='' />
            </div>
            <div className="col-md-3 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactnNativeMap/2.webp'} alt='' />
            </div>
            <div className="col-md-6 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactnNativeMap/3.webp'} alt='' />
            </div>
          </div>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12 text-center">
          <p className="page-text mt-3">{t('reactnativemap.download')}:<br/><a href="https://u.pcloud.link/publink/show?code=XZgg28VZgSs4KMwXypHSFoQJkvj3KVna1DAk" target="_blank" rel="noreferrer">https://u.pcloud.link/publink/show?code=XZgg28VZgSs4KMwXypHSFoQJkvj3KVna1DAk</a></p>
        </div>
      </div>
    </div>
    </>
  );
}

export default ReactNativeCamera
