import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const TsParticlesSnow = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const particlesInit = async (main:any) => {

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };

  return (
    <>
      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/effect/tsparticles"}>tsParticles</Link> / <Link to={langUrl + "/effect/tsparticles/snow"}>Snow</Link>
            </p>
            <h4 className="text-center">Snow</h4>
          </div>
        </div>
      </div>
      <div className="row m-0">
        <div className="tsparticles-wrapper col-md-12">
        <Particles
            id="tsparticles-snow"
            init={particlesInit}
            options={{
              "autoPlay": true,
              "background": {
                "color": {
                  "value": "#708090"
                }
              },
              "fullScreen": {
                "enable": false
              },
              "detectRetina": true,
              "duration": 0,
              "fpsLimit": 60,
              "interactivity": {
                "events": {
                  "resize": true
                }
              },
              "motion": {
                "disable": false,
                "reduce": {
                  "factor": 4,
                  "value": true
                }
              },
              "particles": {
                "bounce": {
                  "horizontal": {
                    "random": {
                      "enable": false,
                      "minimumValue": 0.1
                    },
                    "value": 1
                  },
                  "vertical": {
                    "random": {
                      "enable": false,
                      "minimumValue": 0.1
                    },
                    "value": 1
                  }
                },
                "collisions": {
                  "bounce": {
                    "horizontal": {
                      "random": {
                        "enable": false,
                        "minimumValue": 0.1
                      },
                      "value": 1
                    },
                    "vertical": {
                      "random": {
                        "enable": false,
                        "minimumValue": 0.1
                      },
                      "value": 1
                    }
                  },
                  "enable": false,
                  "mode": "bounce",
                  "overlap": {
                    "enable": true,
                    "retries": 0
                  }
                },
                "color": {
                  "value": "#ffffff",
                  "animation": {
                    "h": {
                      "count": 0,
                      "enable": false,
                      "offset": 0,
                      "speed": 1,
                      "sync": true
                    },
                    "s": {
                      "count": 0,
                      "enable": false,
                      "offset": 0,
                      "speed": 1,
                      "sync": true
                    },
                    "l": {
                      "count": 0,
                      "enable": false,
                      "offset": 0,
                      "speed": 1,
                      "sync": true
                    }
                  }
                },
                "destroy": {
                  "split": {
                    "count": 1,
                    "factor": {
                      "random": {
                        "enable": false,
                        "minimumValue": 0
                      },
                      "value": 3
                    },
                    "rate": {
                      "random": {
                        "enable": false,
                        "minimumValue": 0
                      },
                      "value": {
                        "min": 4,
                        "max": 9
                      }
                    },
                    "sizeOffset": true
                  }
                },
                "life": {
                  "count": 0,
                  "delay": {
                    "random": {
                      "enable": false,
                      "minimumValue": 0
                    },
                    "value": 0,
                    "sync": false
                  },
                  "duration": {
                    "random": {
                      "enable": false,
                      "minimumValue": 0.0001
                    },
                    "value": 0,
                    "sync": false
                  }
                },
                "move": {
                  "angle": {
                    "offset": 0,
                    "value": 90
                  },
                  "attract": {
                    "distance": 200,
                    "enable": false,
                    "rotate": {
                      "x": 600,
                      "y": 600
                    }
                  },
                  "center": {
                    "x": 50,
                    "y": 50,
                    "radius": 0
                  },
                  "decay": 0,
                  "distance": {},
                  "direction": "none",
                  "drift": 0,
                  "enable": true,
                  "gravity": {
                    "acceleration": 9.81,
                    "enable": false,
                    "inverse": false,
                    "maxSpeed": 50
                  },
                  "path": {
                    "clamp": true,
                    "delay": {
                      "random": {
                        "enable": false,
                        "minimumValue": 0
                      },
                      "value": 0
                    },
                    "enable": false,
                    "options": {}
                  },
                  "outModes": {
                    "default": "out",
                    "bottom": "out",
                    "left": "out",
                    "right": "out",
                    "top": "out"
                  },
                  "random": true,
                  "size": false,
                  "speed": 1,
                  "spin": {
                    "acceleration": 0,
                    "enable": false
                  },
                  "straight": false,
                  "trail": {
                    "enable": false,
                    "length": 10,
                    "fillColor": {
                      "value": "#000000"
                    }
                  },
                  "vibrate": false,
                  "warp": false
                },
                "number": {
                  "density": {
                    "enable": true,
                    "area": 800,
                    "factor": 1000
                  },
                  "limit": 0,
                  "value": 160
                },
                "opacity": {
                  "random": {
                    "enable": true,
                    "minimumValue": 0.1
                  },
                  "value": {
                    "min": 0,
                    "max": 1
                  },
                  "animation": {
                    "count": 0,
                    "enable": true,
                    "speed": 1,
                    "sync": false,
                    "destroy": "none",
                    "startValue": "random",
                    "minimumValue": 0
                  }
                },
                "orbit": {
                  "animation": {
                    "count": 0,
                    "enable": false,
                    "speed": 1,
                    "sync": false
                  },
                  "enable": false,
                  "opacity": 1,
                  "rotation": {
                    "random": {
                      "enable": false,
                      "minimumValue": 0
                    },
                    "value": 45
                  },
                  "width": 1
                },
                "reduceDuplicates": false,
                "repulse": {
                  "random": {
                    "enable": false,
                    "minimumValue": 0
                  },
                  "value": 0,
                  "enabled": false,
                  "distance": 1,
                  "duration": 1,
                  "factor": 1,
                  "speed": 1
                },
                "roll": {
                  "darken": {
                    "enable": false,
                    "value": 0
                  },
                  "enable": false,
                  "enlighten": {
                    "enable": false,
                    "value": 0
                  },
                  "mode": "vertical",
                  "speed": 25
                },
                "rotate": {
                  "random": {
                    "enable": false,
                    "minimumValue": 0
                  },
                  "value": 0,
                  "animation": {
                    "enable": false,
                    "speed": 0,
                    "sync": false
                  },
                  "direction": "clockwise",
                  "path": false
                },
                "shape": {
                  "options": {},
                  "type": "circle"
                },
                "size": {
                  "random": {
                    "enable": true,
                    "minimumValue": 1
                  },
                  "value": {
                    "min": 1,
                    "max": 3
                  },
                  "animation": {
                    "count": 0,
                    "enable": false,
                    "speed": 4,
                    "sync": false,
                    "destroy": "none",
                    "startValue": "random",
                    "minimumValue": 0.3
                  }
                }
              },
              "pauseOnBlur": true,
              "pauseOnOutsideViewport": true,
              "responsive": []
            }}
          />
        </div>

      </div>
    </>
  )
}

export default TsParticlesSnow