import { useEffect, useState, useContext, useRef } from "react";
import { Link, useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, EffectFlip, Pagination, Parallax, Navigation } from "swiper";
import { useLoadingContext } from "react-router-loading";
import loadData from "./dapp/data.js";
import { topbar } from "react-router-loading";
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'

const Dapp = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const location = useLocation();
  
  let [searchParams, setSearchParams] = useSearchParams();

  const [dappData, setDappData] = useState<any[]>();
  const [filterDappData, setFilterDappData] = useState<any[]>();
  //const [pending, setPending] = useState<boolean>(true);

  const [category, setCategory] = useState<string>();
  //setCategory(searchFilter!);

  const loadingContext = useLoadingContext();

  topbar.config({
    autoRun: false,
    barThickness: 55,
    barColors: {
        0: 'rgba(255,  255, 0, 1)',
        .3: 'rgba(255,  255, 0, 1)',
        1.0: 'rgba(255, 255,  255,  1)'
    },
    shadowBlur: 5,
    shadowColor: 'red',
    className: 'topbar'
  });

  const loading = async () => {
    setDappData([]);
    setFilterDappData([]);

     // loading some data
    const data = await loadData();
    setDappData(data);


    if(searchParams.toString().length > 0 && data && data.length >0){
      let result = data.filter(d => d.Category === searchParams.get('filter'));
      //console.log("searchParams.get('filter'): " + searchParams.get('filter'));
      //console.log("result: " + result);
      setFilterDappData(result);

    }
    else {
      //console.log("B");
      //console.log(searchParams.toString().length);
      //console.log(dappData);
      //console.log("B");
      setFilterDappData(data);
    }
    

    // call method to indicate that loading is done and we are ready to switch
    loadingContext.done();
  };

  useEffect(() => {
    loading();
  }, [searchParams]);

  let loadingitems=['Item 1','Item 2','Item 3'];

  return (
    <>
      <div className="home-swiper-container">
        <div className="bg-dark pb-4">
          <Swiper
            spaceBetween={30}
            slidesPerView={1.1}
            centeredSlides={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              el: '.home-swiper-pagination',
              clickable: true,
              renderBullet: (index, className) => {
              return '<span class="' + className + '"></span>';
              },
            }}
            parallax={true}
            loop={true}
            navigation={true}
            effect={"flip"}
            modules={[Autoplay, Pagination, Parallax, Navigation]}
            className="home-swiper"
          >
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/1.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/2.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/4.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
          </Swiper>
        </div>
        <div className="home-swiper-pagination" />
      </div>

      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/dapp"}>{t('dapp.title')}</Link>
            </p>
            <h4 className="text-center">{t('dapp.title')}</h4>
          </div>
        </div>
        <div className="mt-3 list row">
          <div className="col-md-12 text-center col-menu">
            <Link to={langUrl + "/dapp"} className={`btn ${searchParams.get('filter') === null ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.all')}</Link>
            <Link to={langUrl + "/dapp?filter=Smart Contract"} className={`btn ${searchParams.get('filter') === "Smart Contract" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.smart_contract')}</Link>
            <Link to={langUrl + "/dapp?filter=Wallet"} className={`btn ${searchParams.get('filter') === "Wallet" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.wallet')}</Link>
          </div>
        </div>
        <div className="mt-3 list row">
          {filterDappData && filterDappData.length > 0
            ? (filterDappData).map(item => (
                <div className="col-md-4 p-2" key={item['Name']}>
                  <div className="card">
                    {item['Blank']
                      ? <a href={item['Url']} className="text-decoration-none" target="_blank" rel="noopener noreferrer">
                          <div className="card-overlay-container">
                            <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                            <div className="card-overlay">
                              <div className="card-overlay-text">
                                <h5 className="mb-4">{item['Name']}</h5>
                                <p>{item['Info-1']}</p>
                                <p>{item['Info-2']}</p>
                                <p>{item['Info-3']}</p>
                                <p>{item['Info-4']}</p>
                              </div>
                            </div>
                            <div className="card-body">
                              <div className="text-center">
                                <h5 className="card-title">{item['Name']}</h5>
                              </div>
                            </div>
                          </div>
                        </a>
                      : <Link to={langUrl + item['Url']} className="text-decoration-none">
                          <div className="card-overlay-container">
                            <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                            <div className="card-overlay">
                              <div className="card-overlay-text">
                                <h5 className="mb-4">{item['Name']}</h5>
                                <p>{item['Info-1']}</p>
                                <p>{item['Info-2']}</p>
                                <p>{item['Info-3']}</p>
                                <p>{item['Info-4']}</p>
                              </div>
                            </div>
                            <div className="card-body">
                              <div className="text-center">
                                <h5 className="card-title">{item['Name']}</h5>
                              </div>
                            </div>
                          </div>
                        </Link>
                      }
                  </div>
                </div>
              ))
            : (loadingitems).map((loadingitems,index)=>(
              <div className="col-md-4 p-2" key={loadingitems[index]}>
                <div className="card">
                  <Skeleton height={200} />
                  <div className="card-body">
                    <div className="text-center">
                      <h5 className="card-title"><Skeleton /></h5>
                    </div>
                  </div>
                </div>
              </div>
            ))
          }
          

        </div>
      </div>
    </>
  )
}

export default Dapp