import { Component, ChangeEvent } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { Web3ReactProvider} from '@web3-react/core';
import { Web3Provider } from "@ethersproject/providers";
import CoinbaseWalletApp from "./coinbasewallet-app.component";

const CoinbaseWallet = () => {
  //#region WalletConnectApp
  function getLibrary(provider:any) {
    //const library = new ethers.providers.Web3Provider(provider);
    //library.pollingInterval = 8000;
    //return library;
    return new Web3Provider(provider);
  }
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
    <Web3ReactProvider getLibrary={getLibrary}>
      <div className="container">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/dapp"}>DAPP</Link> / <Link to={langUrl + "/dapp/walletconnect"}>Interact with Coinbase Wallet</Link>
            </p>
            <h4 className="text-center">Interact with Coinbase Wallet</h4>
            <CoinbaseWalletApp />
          </div>
        </div>
        <div className="mt-3 list row">
          <div className="col-md-12">
          </div>
        </div>
      </div>
    </Web3ReactProvider>
    </>
  );
}

export default CoinbaseWallet