import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';

const ReactNativeQRCodeScanner = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/fullstack"}>{t('full_stack.title')}</Link> / <Link to={langUrl + "/fullstack/reactnativeqrcodescanner"}>QR Code Scanner</Link>
          </p>
          <h4 className="text-center">QR Code Scanner</h4>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12">
          <div className="list row">
            <div className="col-md-12 p-2 page-image">
              <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/icon.png'} alt='' />
            </div>
            <div className="col-md-12 text-center">
              <p className="page-text mt-3">{t('reactnativeqrcodescanner.download')}:<br/><a href="https://u.pcloud.link/publink/show?code=XZljv8VZnou2Y9lLyJubKS9HjDUG3yzPBW6X" target="_blank" rel="noreferrer">https://u.pcloud.link/publink/show?code=XZljv8VZnou2Y9lLyJubKS9HjDUG3yzPBW6X</a></p>
            </div>
          </div>
        </div>
      </div>
      <div className="list row">
        <div className="col-md-12 text-center">
          <p className="page-text mt-3">{t('reactnativemap.screenshot')}:</p>
        </div>
      </div>
      <div className="list row mb-3">
        <div className="col-md-12 p-2 page-image">
          <video width="324" height="720" src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/screenshot.webm'} autoPlay loop>
            <source src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/screenshot.webm'} type='video/webm' />
          </video>
        </div>
        <div className="col-md-12 p-2 page-image">
          <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/1.webp'} alt='' />
        </div>
        <div className="col-md-12 p-2 page-image">
          <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/2.webp'} alt='' />
        </div>
        <div className="col-md-12 p-2 page-image">
          <img src={process.env.PUBLIC_URL + '/images/fullstack/ReactNativeQRCodeScanner/3.webp'} alt='' />
        </div>
      </div>
    </div>
    </>
  );
}

export default ReactNativeQRCodeScanner
