import { useEffect, useState, useContext } from "react";
import { Link, useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, EffectFlip, Pagination, Parallax, Navigation } from "swiper";
import { useLoadingContext } from "react-router-loading";
import loadData from "./fullstack/data.js";
import { topbar } from "react-router-loading";
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';

const FullStack = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const location = useLocation();
  
  let [searchParams, setSearchParams] = useSearchParams();

  //const [data, setData] = useState<any[]>();
  const [fullStackData, setFullStackData] = useState<any[]>();
  const [filterFullStackData, setFilterFullStackData] = useState<any[]>();
  const loadingContext = useLoadingContext();

  topbar.config({
    autoRun: false,
    barThickness: 55,
    barColors: {
        0: 'rgba(255,  255, 0, 1)',
        .3: 'rgba(255,  255, 0, 1)',
        1.0: 'rgba(255, 255,  255,  1)'
    },
    shadowBlur: 5,
    shadowColor: 'red',
    className: 'topbar'
});

  const loading = async () => {
    setFullStackData([]);
    setFilterFullStackData([]);

    const data = await loadData();
    setFullStackData(data);

    let filterCategory = searchParams.get('filter') || '';
    if(searchParams.toString().length > 0 && data && data.length > 0){
      let result = data.filter(d => d.Category.includes(filterCategory));
      setFilterFullStackData(result);
    }
    else {
      setFilterFullStackData(data);
    }

    //setData(data);

    loadingContext.done();
  };

  useEffect(() => {
    loading();
  }, [searchParams]);

  let loadingitems=['Item 1','Item 2','Item 3','Item 4','Item 5','Item 6'];

  return (
    <>
      <div className="home-swiper-container">
        <div className="bg-dark pb-4">
          <Swiper
            spaceBetween={30}
            slidesPerView={1.1}
            centeredSlides={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              el: '.home-swiper-pagination',
              clickable: true,
              renderBullet: (index, className) => {
              return '<span class="' + className + '"></span>';
              },
            }}
            parallax={true}
            loop={true}
            navigation={true}
            effect={"flip"}
            modules={[Autoplay, Pagination, Parallax, Navigation]}
            className="home-swiper"
          >
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/1.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/2.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/4.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
          </Swiper>
        </div>
        <div className="home-swiper-pagination" />
      </div>

      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/fullstack"}>{t('full_stack.title')}</Link>
            </p>
            <h4 className="text-center">{t('full_stack.title')}</h4>
          </div>
        </div>
        <div className="mt-3 list row">
          <div className="col-md-12 text-center col-menu">
            <Link to={langUrl + "/fullstack"} className={`btn ${searchParams.get('filter') === null ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.all')}</Link>
            <Link to={langUrl + "/fullstack?filter=CRUD"} className={`btn ${searchParams.get('filter') === "CRUD" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.crud')}</Link>
            <Link to={langUrl + "/fullstack?filter=Auth"} className={`btn ${searchParams.get('filter') === "Auth" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.auth')}</Link>
            <Link to={langUrl + "/fullstack?filter=QR"} className={`btn ${searchParams.get('filter') === "QR" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.qr')}</Link>
            <Link to={langUrl + "/fullstack?filter=App"} className={`btn ${searchParams.get('filter') === "App" ? "btn-primary" : "btn-outline-primary"} rounded-pill btn-instock ms-2`}>{t('pages.app')}</Link>
          </div>
        </div>
        <div className="mt-3 list row">
          {filterFullStackData && filterFullStackData.length > 0
            ? (filterFullStackData).map(item => (
                <div className="col-md-4 p-2" key={item['Name']}>
                  <div className="card">
                    {item['Blank']
                      ? <a href={item['Url']} className="text-decoration-none" target="_blank" rel="noopener noreferrer">
                          <div className="card-overlay-container">
                            <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                            <div className="card-overlay">
                              <div className="card-overlay-text">
                                <h5 className="mb-4">{item['Name']}</h5>
                                <p>{item['Info-1']}</p>
                                <p>{item['Info-2']}</p>
                                <p>{item['Info-3']}</p>
                                <p>{item['Info-4']}</p>
                              </div>
                            </div>
                            <div className="card-body">
                              <div className="text-center">
                                <h5 className="card-title">{item['Name']}</h5>
                              </div>
                            </div>
                          </div>
                        </a>
                      : <Link to={langUrl + item['Url']} className="text-decoration-none">
                          <div className="card-overlay-container">
                            <img src={process.env.PUBLIC_URL + item['Image']} className="card-img-top" alt={item['Name']} />
                            <div className="card-overlay">
                              <div className="card-overlay-text">
                                <h5 className="mb-4">{item['Name']}</h5>
                                <p>{item['Info-1']}</p>
                                <p>{item['Info-2']}</p>
                                <p>{item['Info-3']}</p>
                                <p>{item['Info-4']}</p>
                              </div>
                            </div>
                            <div className="card-body">
                              <div className="text-center">
                                <h5 className="card-title">{item['Name']}</h5>
                              </div>
                            </div>
                          </div>
                        </Link>
                      }
                  </div>
                </div>
              ))
            : (loadingitems).map((loadingitems,index)=>(
              <div className="col-md-4 p-2" key={loadingitems[index]}>
                <div className="card">
                  <Skeleton height={200} />
                  <div className="card-body">
                    <div className="text-center">
                      <h5 className="card-title"><Skeleton /></h5>
                    </div>
                  </div>
                </div>
              </div>
            ))
          }

        </div>
      </div>
    </>
  )
}

export default FullStack