import { useTranslation } from 'react-i18next';
import { useWeb3React } from '@web3-react/core';
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import { WalletConnectAppEthBalance } from "./walletconnect-app-balance.component";
import {SWRConfig} from "swr";

const WalletConnectApp = () => {

  //#region
  const WalletConnect = new WalletConnectConnector({
      //rpcUrl: `https://mainnet.infura.io/v3/${process.env.INFURA_KEY}`,
      // mainnet: 1 https://mainnet.infura.io/v3/
      // Rinkeby: 4 https://rinkeby.infura.io/v3/
      rpc: {
        1: 'https://mainnet.infura.io/v3/'+process.env.REACT_APP_INFURA_IO_KEY,
        4: 'https://rinkeby.infura.io/v3/'+process.env.REACT_APP_INFURA_IO_KEY,
      },
      //chainId: 4,
      bridge: "https://bridge.walletconnect.org",
      qrcode: true,
      supportedChainIds: [1, 4],
  });

  const { activate, deactivate, active, chainId, account, library } = useWeb3React();

  async function connect() {
    await activate(WalletConnect);
  }

  async function disconnect() {
    await deactivate();
  }

  const fetcher = (library: any) => (...args:any[]) => {
    const [arg1, arg2, ...params] = args
    const method = arg1
    return library[method](arg2, ...params)
  }
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
      {active
        ? <button onClick={disconnect} className="btn btn-success">
            {t('walletconnect.button_disconnect')}
          </button>
        : <button onClick={connect} className="btn btn-success">
            {t('walletconnect.button_connect')}
          </button>
      }
      <h5 className="mt-3">{t('walletconnect.my_account')}:</h5>
      <p className="text-break">{t('walletconnect.address')}: {account}</p>
      <p className="text-break">{t('walletconnect.eth_balance')}: {active && (
            <SWRConfig value={{ fetcher: fetcher(library) }}>
              <WalletConnectAppEthBalance />
            </SWRConfig>
        )}
      </p>
      <p className="text-break">{t('walletconnect.chain_id')}: {chainId}</p>
    </>
  );
}

export default WalletConnectApp