import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const TsParticlesLinks = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const particlesInit = async (main:any) => {

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };

  return (
    <>
      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/effect/tsparticles"}>tsParticles</Link> / <Link to={langUrl + "/effect/tsparticles/links"}>Links</Link>
            </p>
            <h4 className="text-center">Links</h4>
          </div>
        </div>
      </div>
      <div className="row m-0">
        <div className="tsparticles-wrapper col-md-12">
          <Particles
            id="tsparticles-links"
            init={particlesInit}
            options={{
              fullScreen: { enable: false },
              background: {
                color: {
                  value: "#dddddd",
                },
              },
              fpsLimit: 60,
              detectRetina: true,
              interactivity: {
                events: {
                  resize: true,
                }
              },
              particles: {
                color: {
                  value: "#111111",
                },
                links: {
                  color: "#111111",
                  distance: 150,
                  enable: true,
                  opacity: 0.5,
                  width: 1,
                },
                collisions: {
                  enable: true,
                },
                move: {
                  direction: "none",
                  enable: true,
                  outModes: {
                    default: "bounce",
                  },
                  random: false,
                  speed: 3,
                  straight: false,
                },
                number: {
                  density: {
                    enable: true,
                    area: 800,
                  },
                  value: 40,
                },
                opacity: {
                  value: 0.5,
                },
                shape: {
                  type: "square",
                },
                size: {
                  value: { min: 2, max: 8 },
                },
              },
            }}
          />
        </div>

      </div>
    </>
  )
}

export default TsParticlesLinks