import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import { loadSeaAnemonePreset } from "tsparticles-preset-sea-anemone";

const TsParticlesTrails = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  const particlesInit = async (main:any) => {

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };

  const initSeaAnemone = async (main:any) => {
    loadSeaAnemonePreset(main);
  };

  return (
    <>
      <div className="container pb-3">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <p className="mt-3 mb-3 page-navigator">
              <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/effect/tsparticles"}>tsParticles</Link> / <Link to={langUrl + "/effect/tsparticles/trails"}>Trails</Link>
            </p>
            <h4 className="text-center">Trails</h4>
          </div>
        </div>
      </div>
      <div className="row m-0">
        <div className="tsparticles-wrapper col-md-12">
        <Particles
            id="tsparticles-trails"
            init={initSeaAnemone}
            options={{
              fullScreen: { enable: false },
              fpsLimit: 60,
              detectRetina: true,
              preset: "seaAnemone",
              particles: {
                move: {
                  speed: 1
                }
              }
            }}
          />
        </div>

      </div>
    </>
  )
}

export default TsParticlesTrails