import { useState, useEffect, ChangeEvent } from "react";
import { Link } from "react-router-dom";
//import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import ContractMetaMaskService from "../../services/contract-metamask.service";
import 'react-tabs/style/react-tabs.css';
import { ethers } from "ethers";
import { useTranslation } from 'react-i18next';
import { Tab, Tabs } from 'react-bootstrap';

declare let window: any;

const MetaMask = () => {
  //#region State
  const [currentAccount, setCurrentAccount] = useState<string | undefined>();
  const [balance, setBalance] = useState<number | undefined>();
  const [chainId, setChainId] = useState<number | undefined>();
  const [chainName, setChainName] = useState<string | undefined>();
  const [contractName, setContractName] = useState<string | undefined>();
  const [contractSymbol, setContractSymbol] = useState<string | undefined>();
  const [contractMaxSupply, setContractMaxSupply] = useState<number | undefined>();
  const [contractTotalSupply, setContractTotalSupply] = useState<number | undefined>();
  const [contractMaxMintAmount, setContractMaxMintAmount] = useState<number | undefined>();
  const [contractTokenowner, setContractTokenowner] = useState<string | undefined>();
  const [contractBaseURI, setContractBaseURI] = useState<string | undefined>();
  const [contractViewCost, setContractViewCost] = useState<number | undefined>();
  const [formSetCost, setFormSetCost] = useState<string | undefined>();
  const [formWithdraw, setFormWithdraw] = useState<string | undefined>();
  const [hashSetCost, setHashSetCost] = useState<string | undefined>();
  const [hashMint, setHashMint] = useState<string | undefined>();
  const [hashWithdraw, setHashWithdraw] = useState<string | undefined>();
  const [hashWithdrawAll, setHashWithdrawAll] = useState<string | undefined>();
  const [accountCollection, setAccountCollection] = useState<any[]>();
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      window.ethereum.on("chainChanged", handleChainChanged);
    }
  }, []);

  //#region Handle Metamask Change
  function handleAccountsChanged(accounts: any) {
    if (accounts.length === 0) {
      setCurrentAccount(undefined);
      setBalance(undefined);
      setChainId(undefined);
      setChainName(undefined);
    } else if (accounts[0] !== currentAccount) {
      connect();
    }
  }

  function handleChainChanged() {
    window.location.reload();
  }
  //#endregion

  async function connect() {
    let response = await ContractMetaMaskService.connect();
    let data = JSON.parse(response);
    if(data.installmetamask === false) {
      alert("Please install Metamask!");
    }
    else {
      setCurrentAccount(data.userAddress);
      setBalance(data.balance);
      setChainId(data.chainId);
      setChainName(data.chainName);
    }
  }

  async function disconnect() {
    setCurrentAccount(undefined);
    setBalance(undefined);
    setChainId(undefined);
    setChainName(undefined);
    setAccountCollection([]);
  }

  async function getContract() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.getContract();
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setContractName(data.name);
        setContractSymbol(data.symbol);
        setContractMaxSupply(data.maxSupply);
        setContractTotalSupply(data.totalSupply);
        setContractMaxMintAmount(data.maxMintAmount);
        setContractTokenowner(data.owner);
        setContractBaseURI(data.baseURI);
        setContractViewCost(data.viewCost);
      }
    }
  }

  async function getAccountCollection() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.getAccountCollection();
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setAccountCollection(data.collection);
      }
    }
  }

  function onChangeSetCost(e: ChangeEvent<HTMLInputElement>) {
    setFormSetCost(e.target.value);
  }

  function onChangeWithdraw(e: ChangeEvent<HTMLInputElement>) {
    setFormWithdraw(e.target.value);
  }

  async function setCost() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.setCost(formSetCost);
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setHashSetCost(data.tx.hash);
      }
    }
  }

  async function mint() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.mint();
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setHashMint(data.tx.hash);
      }
    }
  }

  async function withdraw() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.withdraw(formWithdraw);
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setHashWithdraw(data.tx.hash);
      }
    }
  }

  async function withdrawAll() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else {
      let response = await ContractMetaMaskService.withdrawAll();
      let data = JSON.parse(response);
      if(data.installmetamask === false) {
        alert("Please install Metamask!");
      }
      else {
        setHashWithdrawAll(data.tx.hash);
      }
    }
  }

  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/dapp"}>DAPP</Link> / <Link to={langUrl + "/dapp/metaMask"}>Interact with MetaMask</Link>
          </p>
          <h4 className="text-center">Interact with MetaMask</h4>
        </div>
      </div>
      <div className="mt-3 list row">
        <div className="col-md-12">
          {currentAccount
            ? <button onClick={disconnect} className="btn btn-success">
                {t('metamask.button_disconnect')}
              </button>
            : <button onClick={connect} className="btn btn-success">
                {t('metamask.button_connect')}
              </button>
          }
          <h5 className="mt-3">{t('metamask.my_account')}:</h5>
          <p className="text-break">{t('metamask.address')}: {currentAccount}</p>
          <p className="text-break">{t('metamask.eth_balance')}: {balance}</p>
          <p className="text-break">{t('metamask.chain_id')}: {chainId}</p>
          <p className="text-break">{t('metamask.chain_name')}: {chainName}</p>

          {/*
          <Tabs>
            <TabList>
              <Tab>ETH Rinkeby (ERC-721)</Tab>
              <Tab>Polygon Mumbai (ERC-721)</Tab>
            </TabList>
            
            <TabPanel>
              <h4>HsNum_BETA_v4</h4>
              <p>Chain: ETH Rinkeby<br />Format: ERC-721</p>
              <div className="form-group">
                <a href="https://rinkeby.etherscan.io/address/0x4a6c46496524171c955813881a4308689b638df4" rel="noopener noreferrer" target="_blank">View Contract</a> - <a href="https://testnets.opensea.io/collection/hsnum-beta-v4" rel="noopener noreferrer" target="_blank">View Opensea</a>
              </div>
              
              {currentAccount
                ? <button onClick={disconnect} className="btn btn-success">
                    {t('metamask.button_disconnect')}
                  </button>
                : <button onClick={connect} className="btn btn-success">
                    {t('metamask.button_connect')}
                  </button>
              }
              <button onClick={getContract} className="ms-1 btn btn-success">
                {t('metamask.button_getcontract')}
              </button>
              <button onClick={getAccountCollection} className="ms-1 btn btn-success">
                {t('metamask.button_collection')}
              </button>
              <h5 className="mt-3">My Account:</h5>
              <p>Address: {currentAccount}</p>
              <p>ETH Balance: {balance}</p>
              <p>Chain Id: {chainId}</p>
              <p>Chain Name: {chainName}</p>
              <h5 className="mt-3">Contract Info:</h5>
              <p>Name: {contractName}</p>
              <p>Symbol: {contractSymbol}</p>
              <p>Max Supply: {contractMaxSupply}</p>
              <p>Total Supply: {contractTotalSupply}</p>
              <p>Max Mint Amount: {contractMaxMintAmount}</p>
              <p>Token Ower: {contractTokenowner}</p>
              <p>Base URI: {contractBaseURI}</p>
              <p>View Cost: {contractViewCost} ETH</p>
              <input type="text" className="form-control w-50" id="setcost" name="setcost" value={formSetCost} onChange={onChangeSetCost} />
              <button onClick={setCost} className="mt-1 btn btn-danger">
                Set Cost
              </button>
              <p>Tx Hash: {hashSetCost}</p>
              <button onClick={mint} className="mt-3 btn btn-danger">
                Mint
              </button>
              <p>Tx Hash: {hashMint}</p>
              <input type="text" className="form-control mt-3 w-50" id="withdraw" name="withdraw" value={formWithdraw} onChange={onChangeWithdraw} />
              <button onClick={withdraw} className="mt-1 btn btn-danger">
                Withdraw
              </button>
              <p>Tx Hash: {hashWithdraw}</p>
              <button onClick={withdrawAll} className="mt-3 btn btn-danger">
                Withdraw All
              </button>
              <p>Tx Hash: {hashWithdrawAll}</p>
              <h5 className="mt-3">My Collection:</h5>
              <ul>   
              {accountCollection 
                ? (accountCollection).map(item => (
                  <li key={item['hex']}>Token ID: {parseInt(item['hex'])} - <a href={"https://testnets.opensea.io/assets/0x4a6c46496524171c955813881a4308689b638df4/".concat(parseInt(item['hex']).toString())} rel="noopener noreferrer" target="_blank">View</a></li>
                ))
                : null
              }
              </ul>
            </TabPanel>

            <TabPanel>
              <h4>HsNum_BETA_v3</h4>
              <p>Chain: Polygon Mumbai<br />Format: ERC-721</p>
              <div className="form-group">
                <a href="https://mumbai.polygonscan.com/address/0x731505f1bcf543aba6828d43bd4247f753a3be22" rel="noopener noreferrer" target="_blank">View Contract</a> - <a href="https://testnets.opensea.io/collection/hsnum-beta-v3" rel="noopener noreferrer" target="_blank">View Opensea</a>
              </div>
            </TabPanel>
          </Tabs>
          */}
        </div>
      </div>
    </div>
    </>
  );
}

export default MetaMask
